import { Switch, Route } from "react-router-dom";
import Index from "./pages/index";
import Result from "./pages/result";

import "./App.less";

function App() {
  return (
    <div className="App">
      <Switch>
        <Route path="/result" component={Result} />
        <Route path="/" component={Index} />
      </Switch>
    </div>
  );
}

export default App;
