import domtoimage from "dom-to-image";
import { useEffect, useState } from "react";
import { GenSvg } from "../../assets/SvgPath";

export default function Result() {
  const [imgData, setImgData] = useState("");

  useEffect(() => {
    const svgTag = document.getElementById("role-svg");
    console.log("🚀 ~ file: index.tsx ~ line 10 ~ useEffect ~ svgTag", svgTag);
    if (svgTag) {
      domtoimage
        .toPng(svgTag)
        .then(function (dataUrl) {
          console.log("🚀 ~ file: index.tsx ~ line 13 ~ dataUrl", dataUrl);
          setImgData(dataUrl);
        })
        .catch(function (error) {
          console.error("oops, something went wrong!", error);
        });
    }
  }, []);

  return (
    <div>
      {/* <GenSvg />
      <img src={imgData} alt="" /> */}
      <img
        src="data:image/png;base64,"
        alt=""
      />
    </div>
  );
}
